.return-button{display:flex;flex-direction:row;justify-content:flex-start;align-items:center;padding:10px 30px;height:40px;width:260px;background-color:#000;color:#f0f0f0;border-radius:8px;cursor:pointer;font-size:18px}.return-button img{transition:ease-in-out .5s;margin:0 10px 0 0}.return-button:hover img{transform:translateX(-12px)}.return-button p{margin-top:-2px}.error-message{color:#ffc4c7;text-align:center;font-size:18px;margin-top:3px}.checkbox{margin-top:10px;height:375px;color:#fff;padding:10px 20px;border-radius:5px}.checkbox .title{font-size:20px;font-weight:600;margin-bottom:10px}.checkbox .input:not(:checked),.checkbox .input:checked{opacity:0;width:0;height:0}.checkbox .label{position:relative;padding-left:35px;font-size:17px;font-family:inherit;line-height:2;cursor:pointer}.checkbox .label::before{content:"";position:absolute;left:0;top:-3px;width:24px;height:24px;border:1px solid #9e9e9e;background:#fff;border-radius:3px;box-shadow:inset 0 1px 3px #000,0 0 0 #08f;transition:all .275s}.checkbox .label::after{content:"✓";position:absolute;top:10px;left:5px;font-size:24px;color:#08f;line-height:0;transition:all .2s}.checkbox .input:not(:checked)+label::after{opacity:0;transform:scale(0) rotate(45deg)}.checkbox .input:checked+label::after{opacity:1;transform:scale(1) rotate(0)}.checkbox .input:checked:focus+label::before,.checkbox .input:not(:checked):focus+label::before{box-shadow:inset 0 1px 3px #000,0 0 0 6px #08f}